body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-lg: 18px;
  --font-size-9xl: 28px;

  /* Colors */
  --color-white-100: #fff;
  --color-palevioletred: #ff79a2;
  --color-black: #000;
  --color-dimgray: #575757;
  --color-gray: #292929;

  /* border radiuses */
  --br-3xs: 10px;
}
